import { Component, OnDestroy, OnInit } from '@angular/core';
import { APP_ROUTE, BER_EXEMPT } from '@core/constants';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { AisleType, SellerType } from '@core/enums';
import { Ad } from '@core/models';
import { AbstractAdItemComponent } from '../abstracts/ad-item.abstract';
import { AdActionsComponent } from '@feature/ad-detail/component/ad-actions/ad-actions.component';

interface BadgeProperty {
	property: string;
	badgeType: string;
}
@Component({
    selector: 'findqo-ad-featured-item',
    templateUrl: './ad-featured-item.component.html',
    styleUrls: ['./ad-featured-item.component.scss', '../styles/ad-item.component.scss'],
    standalone: false
})
export class AdFeaturedItemComponent extends AbstractAdItemComponent implements OnInit {
	constructor(private readonly adsApi: AdsApi) {
		super();
	}

	showSellerInfo = false;
	isFavoritesBtnEnable = false;
	readonly berExempt = BER_EXEMPT;
	readonly aisleType = AisleType;
	readonly defaultCountryCode = '+353';
	isPhoneClicked = false;

	badgeProperties: BadgeProperty[] = [
		{ property: 'bedroomCount', badgeType: 'bedroom' },
		{ property: 'bathroomCount', badgeType: 'shower' },
		{ property: 'propertySize', badgeType: 'property-size' },
	];

	checkRoute(): void {
		if (this.router.url.includes(APP_ROUTE.favorites)) {
			this.showSellerInfo = true;
			this.isFavoritesBtnEnable = true;
		} else if (this.router.url.includes(APP_ROUTE.seller + '/')) {
			this.showSellerInfo = true;
		}
	}

	override ngOnInit(): void {
		this.isPhoneClicked = false;
		super.ngOnInit();
		this.checkRoute();
	}
}
